@font-face {
  font-family: "averto";
  font-style: normal;
  font-weight: 400;
  src: url(/themes/myproject/fonts/averto/Averto.ttf);
}
.footer__container {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.footer__container-li {
  list-style: none;
  margin-right: 50px;
}
.footer__container-li a {
  font-family: "averto", sans-serif;
  font-size: 14px;
  line-height: 28px;
  font-weight: 400;
  color: #000;
  text-decoration: none;
}

.footer__container-ul {
  padding: 0;
}

.footer__container .menu-level-0 > a {
  font-size: 20px;
  font-weight: 400;
  line-height: 28px;
  color: #000;
}

.footer__menu__logo-des p {
  margin-right: 120px;
}

.footer__menu__logo-links {
  display: flex;
}
.footer__menu__logo-links a {
  text-decoration: none;
}

.footer__container-block {
  position: relative;
  top: 12rem;
  margin-left: 120px;
}

.footer__container-descrip {
  font-family: "averto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  width: 270px;
  color: #565656;
}

.footer__container-sociallinks div {
  display: flex;
}
.footer__container-sociallinks a {
  text-decoration: none;
}